import React, { Component } from "react";
import { Container, Col, Row } from "react-bootstrap";
import { useLocation } from "react-router-dom";

class Users extends Component {
  render() {
    return (
      <React.Fragment>
        <Container>
          <Row className="justify-content-center">
            <Col className="mt-3" lg={6}>
              <div
                className="card border-dark"
                style={{
                  borderRadius: "25px",
                  boxShadow: "0px 5px 8px grey",
                }}
              >
                <div
                  style={{
                    borderTopLeftRadius: "25px",
                    borderTopRightRadius: "25px",
                  }}
                  className="card-header bg-dark text-white text-center"
                >
                  <b>User List</b>
                </div>
                <ul className="list-group list-group-flush">
                  {this.props.Users.map((User, index) => (
                    <li
                      key={User.id}
                      className="list-group-item"
                      style={{
                        borderBottomLeftRadius: "25px",
                        borderBottomRightRadius: "25px",
                      }}
                    >
                      <div className="row text-left">
                        <label className="col-12 col-sm-6 text-left pl-3">
                          <b>
                            {User.id}. {User.FirstName} {User.LastName}
                          </b>
                        </label>
                      </div>
                    </li>
                  ))}
                </ul>
              </div>
            </Col>
            <Col className="mt-3" lg={6}>
              <UrlQuery />
            </Col>
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}

function useQuery() {
  return new URLSearchParams(useLocation().search);
}

function UrlQuery() {
  const QueryParams = useQuery();
  const itemStyle = {
    borderBottomLeftRadius: "25px",
    borderBottomRightRadius: "25px",
  };
  return (
    <div
      className="card border-dark"
      style={{
        borderRadius: "25px",
        boxShadow: "0px 5px 8px grey",
      }}
    >
      <div
        style={{
          borderTopLeftRadius: "25px",
          borderTopRightRadius: "25px",
        }}
        className="card-header bg-dark text-white text-center"
      >
        <b>URL Query</b>
      </div>
      <ul className="list-group list-group-flush">
        <li className="list-group-item" style={itemStyle}>
          <label className="col-12 col-sm-6 text-left pl-3">
            <b>pageno : {QueryParams.get("pageno") || 1} </b>
          </label>
        </li>
        <li className="list-group-item" style={itemStyle}>
          <label className="col-12 col-sm-6 text-left pl-3">
            <b>Sort : {QueryParams.get("sort") || "id"} </b>
          </label>
        </li>
        <li className="list-group-item" style={itemStyle}>
          <label className="col-12 col-sm-6 text-left pl-3">
            <b>page size : {QueryParams.get("pagesize") || 10} </b>
          </label>
        </li>
      </ul>
    </div>
  );
}

export default Users;
