import React from "react";
import { Col, Card, Row, Button } from "react-bootstrap";
import { Link, useParams } from "react-router-dom";

function PostContent(props) {
  let { postID } = useParams();

  return (
    <React.Fragment>
      <Row className="justify-content-center">
        <Col className="mt-3" lg={7}>
          <div
            className="card bg-light border-dark"
            style={{
              borderRadius: "25px",
              boxShadow: "0px 5px 8px grey",
            }}
          >
            <div
              className="card-header bg-dark text-white text-center"
              style={{
                borderTopLeftRadius: "25px",
                borderTopRightRadius: "25px",
              }}
            >
              <h4>Post {props.posts[postID - 1].id}</h4>
            </div>
            <Card
              style={{
                borderBottomLeftRadius: "25px",
                borderBottomRightRadius: "25px",
              }}
            >
              <Card.Body>
                <Card.Title className="text-center">
                  {props.posts[postID - 1].title}
                </Card.Title>
                <Card.Text>{props.posts[postID - 1].content}</Card.Text>
                <Link to="/posts">
                  <Button
                    variant="outline-primary"
                    style={{
                      borderRadius: "25px",
                    }}
                  >
                    Back
                  </Button>
                </Link>
              </Card.Body>
            </Card>
          </div>
        </Col>
      </Row>
    </React.Fragment>
  );
}

export default PostContent;
